.timer {
  display: inline-flex;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #424242;
  width: 600px;
  height: 100px;
  margin: auto;
}

.field {
  display: flex;
  height: 70px;
  width: 270px;
  font-size: 50px;
}

.label {
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;

  color: #009688;
  padding: 2px 6px;
  border-radius: 2px;
}
